import React from 'react'
import Styles from './Styles'
import { Form } from 'react-final-form'
import { Field } from 'react-final-form-html5-validation'
//import UserService from '../services/user-service/'

const sleep = ms => new Promise(resolve => setTimeout(resolve, ms))

const onSubmit = async values => {
    await sleep(300)
    window.alert(JSON.stringify(values, 0, 2))
    


}

const Register = () => (
    
    <Styles>
        <Form
            onSubmit={onSubmit}
            render={({ handleSubmit, reset, submitting, pristine, values }) => (
                <form onSubmit={handleSubmit}>
                    <div>
                    <label>Username</label>
                        <Field
                            name="Username"
                            component="input"
                            type="text"
                            placeholder="Username"
                            required
                            minLength={4}
                            tooShort="You need a longer name"
                            valueMissing="Username missing!">
                        </Field>
                    </div>
                    <Field
                        name="Password"
                        type="password"
                        autocomplete="password"
                        placeholder="Password"
                        required
                        valueMissing="Password missing!">
                        {({ input, meta: { error, touched }, ...rest }) => (
                            <div>
                                <label>Password</label>
                                <input {...input} {...rest} />
                                {error && touched && <span>{error}</span>}
                            </div>
                        )}
                    </Field>
                    <Field
                        name="Re-Password"
                        autocomplete="re-password"
                        type="password"
                        placeholder="Re-Password"
                        pattern= {values["Password"]}
                        patternMismatch="Passwords must match!"
                        required
                        valueMissing="Re-Password missing!">
                        {({ input, meta: { error, touched }, ...rest }) => (
                            <div>
                                <label>Re-Password</label>
                                <input {...input} {...rest} />
                                {error && touched && <span>{error}</span>}
                            </div>
                        )}
                    </Field>
                    <div>
                        <label>Email</label>
                        <Field
                            name="Email"
                            type="Email"
                            typeMismatch="That's not an Email address"
                            component="input"
                            placeholder="Email"
                            required
                        />
                    </div>
                    <div>
                        <label>Feedback</label>
                        <Field name="notes" component="textarea" placeholder="We are constantly trying to impove our customer's experience. Feel free to share your ideas here! " />
                    </div>
                    <div className="buttons">
                        <button type="submit" disabled={submitting}>
                            Register
            </button>
                    </div>
                    <pre>{JSON.stringify(values, 0, 2)}</pre>
                </form>
            )}
        />
    </Styles>


)

export default Register